.App {
  text-align: center;
}
div {
  box-sizing: border-box;
}

.list{
  text-align: left;
  padding-left: 20px;
  border-bottom: 1px solid gray;
}
.one-line{
  display: flex;
  place-items: center; /*세로 가운데 정렬*/
}
.black-nav{
  display: flex;
  background-color: black;
  width: 100%;
  color: white;
  padding-left: 20px;
}
.btn{
  height: 30px;
  margin-left: 10px;
  border: none;
  background-color: black;
  color: white;
  padding: 5px;
  border-radius: 5px;
  cursor: pointer;
}
.App-logo {
  height: 40vmin;
  pointer-events: none;
}
.sort-box{
  padding: 20px 20px 20px 0;
  display: flex;
  justify-content: space-between;
  border-bottom: 1px solid rgba(128, 128, 128, 0.2);
}

.likes-btn{
  cursor: pointer;
}

.modal{
  margin-top: 20px;
  padding: 20px;
  background: #eee;
  text-align: left;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
